<template>
    <link href="https://cdn.jsdelivr.net/npm/bootstrap@5.3.3/dist/css/bootstrap.min.css" rel="stylesheet"
        integrity="sha384-QWTKZyjpPEjISv5WaRU9OFeRpok6YctnYmDr5pNlyT2bRjXh0JMhjY6hW+ALEwIH" crossorigin="anonymous">
    <div class=" container card  mt-5">
        <h2  class="titleh1">Agregar Nuevo Proveedor</h2>
        <div class="form-group m-2">
            <label class="col-4  col-md-3 label" for="nombre"> Nombre</label>
            <input class="col-6 col-md-3 cajasTexto" v-model="newRegris.nombre" type="text" id="nombre" placeholder="Nombre">
        </div>
        <div class="form-group m-2">
            <label class="col-4 col-md-3 label " for="empresa"> Empresa</label>
            <input class="col-6 col-md-3 cajasTexto " v-model="newRegris.empresa" type="text" id="empresa" placeholder="Empresa">
        </div>
        <div class="form-group m-2">
            <label class="col-4 col-md-3 label" for="placas">Placas</label>
            <input class="col-6 col-md-3 cajasTexto"  v-model="newRegris.placas" type="text" id="placas" placeholder="Placas" />
        </div>
        <div class="form-group m-2">
            <label class="col-4 col-md-3 label" for="tipoVehiculo">Tipo de vehiculo</label>
            <input class="col-6 col-md-3 cajasTexto"   v-model="newRegris.tipoVehiculo" type="text" id="tipoVehiculo" placeholder="Tipo de vehiculo" />
        </div>
        <div class="form-group m-2">
            <select class="col-9 col-md-6 cajas"   v-model="newRegris.area" name="" id="">
                <option value="">Selecciona un departamento</option>
                <option value="Recursos Humanos">Recursos Hermanos</option>
                <option value="Almacen">Almancen</option>
            </select>
        </div>

        <div class="form-group m-2">
            <label class="col-4 col-md-3 label">Fecha</label>
            <input class="col-6 col-md-3" v-model="newRegris.fechaActual" type="date" id="fechActual" disabled>
        </div>
        <div class="form-group m-2">
            <label class="col-4 col-md-3 label" for="">Hora de entrada</label>
            <input class="col-6 col-md-3" v-model="newRegris.horaActual" type="time" id="horActual" disabled>

        </div>

        <div class="m-2">
            <button class="submit-button col-6  col-md-3 m-1 button " :disabled="isButtonDisable" @click="registerData">Regristar</button>
            <button class="cancel-button col-3  col-md-3 m-1 button"  @click="goToCancelar" >Cancelar</button>
        </div>
    </div>
</template>

<script>
import { getFirestore, collection, addDoc, orderBy, query, onSnapshot } from 'firebase/firestore';
import { initializeApp } from 'firebase/app';
import { getAnalytics } from 'firebase/analytics';
import { ref, onUnmounted, resolveTransitionHooks } from 'vue';

const firebaseConfig = {
    apiKey: "AIzaSyA_uWz4tJnzrpgSID-eR--M58jPzsfowDg",
    authDomain: "regirstroproesa.firebaseapp.com",
    databaseURL: "https://regirstroproesa-default-rtdb.firebaseio.com",
    projectId: "regirstroproesa",
    storageBucket: "regirstroproesa.appspot.com",
    messagingSenderId: "973196483901",
    appId: "1:973196483901:web:57e3da488b032279b67954"
};

const app = initializeApp(firebaseConfig);
const analytics = getAnalytics(app);
const db = getFirestore(app);
const regrist = ref([]);

export default {
    name: 'Registro',
    data() {
        return {
            newRegris: {
                nombre: '',
                empresa: '',
                placas: '',
                tipoVehiculo: '',
                area: '',
                fechaActual: '',
                horaEntrada: '',
            },
        };
    },
    computed: {
        isFormValid() {
            return (
                this.newRegris.nombre &&
                this,this.newRegris.empresa &&
                this.newRegris.placas &&
                this.newRegris.tipoVehiculo &&
                this.newRegris.area &&
                this.newRegris.horaActual &&
                this.newRegris.fechaActual
            );
        },
        isButtonDisable() {
            return !this.isFormValid;
        },
    },
    mounted() {
        this.obtenerHora();
        setInterval(() => {
            this, this.obtenerHora();
        }, 1000);

        const regrisQuery = query(collection(db, 'registro'), orderBy('nombre'));
        const unsubscribe = onSnapshot(regrisQuery, (snapshot) => {
            regrist.value = snapshot.docs.map((doc) => ({
                id: doc.id,
                ...doc.data(),
            }));
        });
        onUnmounted(unsubscribe);
    },
    methods: {
        registerData() {
            if (this.isFormValid) {
                addDoc(collection(db, 'registro'), this.newRegris)
                    .then(() => {
                        console.log('Data added successfully!');
                        this.goToRegistar();
                    })
                    .catch((error) => {
                        console.error('Error adding document: ', error);
                    });
            }
        },
        obtenerHora() {
            const fecha = new Date();
            const hora = fecha.getHours();
            const minutos = fecha.getMinutes();
            const formatoHora = `${hora.toString().padStart(2, '0')}:${minutos.toString().padStart(2, '0')}`;
            this.newRegris.horaActual = formatoHora;
            const formatofecha = fecha.toISOString().slice(0, 10);
            this.newRegris.fechaActual = formatofecha;



        },
        goToRegistar() {
            this.$router.push('/');
        },
        goToCancelar() {
            this.$router.push('/');
        },
    }
}
</script>

<style>
.titleh1{
    font-family : Montserrat, sans-serif;
    font-size: 20px;
}
.cajasTexto{
    font-family : Montserrat, sans-serif;
    font-size: 15px;
    border-radius: 4px;
}
.label{
    font-family : Montserrat, sans-serif;
    font-size: 15px;
}
.button{
     font-family : Montserrat, sans-serif;
    font-size: 15px;
}

.select{
    font-family : Montserrat, sans-serif;
    font-size: 15px;
}
</style>